.normalKeyLists{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 5px;
}

.normalKeyLists li{
  -webkit-flex: 0 0 70px;
          flex: 0 0 70px;
  height: 45px;
  line-height: 45px;
  margin: 4px 0;
  text-align: center;
  border: 1px solid #d1d1d1;
}

.normalKeyLists li:hover{
  background: #fff;
  cursor: pointer;
}


.panel {
  padding: 10px;
  height: 120px;
  border: 1px solid green;
  text-align: right;
}

.panel .wrap {
  width:100%;
  margin: 5px 0;
  font-size: 18px;
  overflow: hidden;
}

.panel .inputValue {
  margin-top: 15px;
  font-size: 40px;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

ul li{
  list-style: none;
}

a{
  text-decoration: none;
  color: #000;
}

.App {
  width: 300px;
  min-height: 450px;
  margin: 40px auto;
  border: .1px solid #dbd9d9;
  background: #f1f1f1;
}

.App > header {
  height: 30px;
  line-height: 30px;
  text-align: center ;
  font-size: 14px;
}

.App > .mode {
  height: 30px;
  line-height: 30px;
  padding: 0 5px;
  font-size: 14px;
}

.mode .cmode {
  padding: 4px 5px;
  border: none;
  outline: none;
  border-radius:  4px;
  background: none;
}

.cmode:hover{
  background: #fff;
}

.cmode.active {
  font-weight: bold;
}


